module.exports = "<!-- LIST -->\r\n<div class=\"row\">\r\n    <div class=\"col-md-6\">\r\n        <div class=\"input-group mb-2 mr-sm-2 mb-sm-0\">\r\n            <input type=\"text\" class=\"form-control\" id=\"inlineFormInputGroup\" placeholder=\"Buscar\" [(ngModel)]=\"search\" (keydown)=\"enter($event)\">\r\n            <div style=\"width: 34px\" class=\"input-group-addon\">\r\n                <a style=\"cursor:pointer\" (click)=\"getAll()\">\r\n                    <i class=\"fa fa-search\"></i>\r\n                </a>\r\n            </div>\r\n        </div>\r\n    </div>\r\n    <div class=\"col-md-6\">\r\n        <button md-raised-button (click)=\"NEW()\" color=\"primary\" class=\"btn-w-md no-margin-left btn-right\">Nuevo</button>\r\n    </div>\r\n\r\n    <div class=\"col-sm-12\">\r\n\r\n        <div class=\"box box-default table-box table-responsive mdl-shadow--2dp\">\r\n\r\n            <table class=\"mdl-data-table table-bordered table-striped cf no-margin\">\r\n                <thead>\r\n                    <tr>\r\n                        <th class=\"noAuto\" (click)=\"sort('id')\">ID\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='id'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n                        <th  (click)=\"sort('barcode')\">Código de barras\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='barcode'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n                        <th (click)=\"sort('comercial_name')\">Nombre Comercial\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='comercial_name'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n                        <th (click)=\"sort('units')\">Inventario\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='units'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n\r\n                        <th (click)=\"sort('state')\">Estado\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='state'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n                        <th class=\"w-40\"></th>\r\n\r\n                    </tr>\r\n                </thead>\r\n                <tbody>\r\n                    <tr *ngFor=\"let item of list.data\">\r\n                        <td class=\"noAuto\">{{ item.id }}</td>\r\n                        <td>{{ item.barcode }}</td>\r\n                        <td>{{ item.comercial_name }}</td>\r\n                        <td>{{ item.units }}</td>\r\n                        <td>{{ item.status ? 'Activo' : 'Inactivo' }}</td>\r\n                        <td class=\"w-40\">\r\n                            <button type=\"button\" md-icon-button class=\"text-primary\" (click)=\"view(item)\">\r\n                                <md-icon>mode_edit</md-icon>\r\n                            </button>\r\n                        </td>\r\n                    </tr>\r\n                </tbody>\r\n            </table>\r\n\r\n        </div>\r\n        <ngb-pagination [pageSize]=\"pageSize\" [collectionSize]=\"paginationSize\" [(page)]=\"advancedPagination\" [maxSize]=\"6\" [rotate]=\"true\"\r\n            [ellipses]=\"false\" [boundaryLinks]=\"true\" (pageChange)=\"getAll()\"></ngb-pagination>\r\n\r\n    </div>\r\n</div>"

