module.exports = "<section class=\"chapter\">\r\n    <section class=\"hero\">\r\n        <div class=\"hero-content\">\r\n            <h1 class=\"hero-title\">Productos</h1>\r\n        </div>\r\n        <p class=\"hero-tagline\">A través de esta opción, usted podrá crear los productos de las cuales son necesarias para...</p>\r\n    </section>\r\n    <article class=\"article padding-lg-v article-dark article-bordered\">\r\n        <div class=\"container-fluid with-maxwidth\">\r\n            <div class=\"box box-default\">\r\n                <div class=\"box-body\">\r\n                   \r\n                    <product-action-cmp *ngIf=\"isOpenActions\" [numId]=\"id\"></product-action-cmp>\r\n                    <product-list-cmp *ngIf=\"isOpenList\"></product-list-cmp>\r\n                   \r\n                </div>               \r\n            </div>\r\n        </div>\r\n    </article>\r\n</section>"

